.card {
  margin: 20px;
  width: 18rem;
  background: transparent;
  background-image: linear-gradient(-45deg, #ffc7960c 0%, #ff6b9557 100%);
  font-family: 'Orelega One', cursive;
  color: aliceblue;
  text-shadow: 1px 1px 3px black;
  border: 2px solid rgba(255, 255, 255, 0.199);
  border-radius: 1em;
  box-shadow: 2px 2px 10px white inset;
  backdrop-filter: blur(15px);
}

.card-title {
  margin-bottom: 15px;
  font-size: 1.5rem;
}

#closeIcon {
  display: flex;
  justify-content: flex-end;
  margin-right: 8px;
  margin-top: 8px;
  
}
.close-button{
  border-radius: 100%;
}

.card-body {
  margin-top: 0px !important;
  padding-top: 0px !important;

}
.card p{
  font-size: 1.2rem;
}
.button{
  border-radius: 0.5em;
  padding-top: 0.2em;
  padding-bottom: 0.2em;
  padding-left: 1em;
  padding-right: 1em;
  outline: none;
  background-color: rgba(255, 251, 251, 0.1) !important;
  color: aliceblue;
  text-shadow: 1px 1px 3px black;
  box-shadow: 2px 2px 10px white inset;
  font-size: 0.9rem;
  min-height: 2.7em;
  font-family: 'Orelega One', cursive;
  backdrop-filter: blur(15px);
}
.button:active{
  outline: none;
}
.button:focus{
  outline: none;
}

.customPopup{
  width: 20em !important;
  height: 20em;
  border-radius: 1em !important;
  background: transparent !important;
  background-image: linear-gradient(-45deg, #ffc7960c 0%, #ff6b9557 100%) !important;
  color: aliceblue;
  text-shadow: 1px 1px 3px black;
  border: 2px solid rgba(255, 255, 255, 0.199) !important;
  box-shadow: 2px 2px 10px white inset;
  backdrop-filter: blur(15px) !important;
}
.parrafo{
  color: white;
  font-family: 'Orelega One', cursive;
  text-shadow: 2px 2px 4px black;
}
.titulo{
  color: white;
  font-family: 'Orelega One', cursive;
  text-shadow: 2px 2px 4px black;
}
